import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/pages/index'
// import index from '@/pages/index/index'//
// import Bindphone from '@/pages/bindphone'//
// import Register from '@/pages/register'//
// import Coupons from '@/pages/coupons'//
// import Myrepair from '@/pages/myRepair/myrepair'//
// import OrdersDetail from '@/pages/myRepair/ordersDetail'//
// import cancelOrders from '@/pages/myRepair/cancelOrders'//
// import OperatorOrders from '@/pages/myRepair/operatorOrders'//
// import OperatorOrdersDetail from '@/pages/myRepair/operatorOrdersDetail'//
// import OperatorRepairSuccess from '@/pages/myRepair/operatorRepairSuccess'// 
// import Notices from '@/pages/notices'//
// import MyPublish from '@/pages/communities/myPublish'//
// import ThreadDetail from '@/pages/communities/threadDetail'//
// import Addresses from '@/pages/addresses'//
// import Abort from '@/pages/abort'//

// import kaitong from '@/pages/index/kaitong'
Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: resolve=> require(['@/pages/index'],resolve),
      children:[
        {path:'',component:resolve=>require(['@/pages/index/index'],resolve),
          meta: {
            title:''
          }
        }
      ]
    },
    {
      path:'/register',
      name:'register',
      component: resolve=>require(['@/pages/register'],resolve),
      meta: {
        title:''
      }
    },

    {
      path:'/bindphone',
      name:'bindphone',
      component:resolve=> require(['@/pages/bindphone'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/coupons',
      name:'coupons',
      component:resolve=> require(['@/pages/coupons'],resolve),
      meta:{
        title:''
      }
    }, 
    {
      path:'/myrepair',
      name:'myrepair',
      component:resolve=> require(['@/pages/myRepair/myrepair'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/ordersDetail',
      name:'ordersDetail',
      component:resolve=> require(['@/pages/myRepair/ordersDetail'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/cancelorders',
      name:'cancelorders',
      component:resolve=> require(['@/pages/myRepair/cancelOrders'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/notices',
      name:'notices',
      component:resolve=> require(['@/pages/notices'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/myPublish',
      name:'myPublish',
      component:resolve=> require(['@/pages/communities/myPublish'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/threadDetail',
      name:'threadDetail',
      component:resolve=> require(['@/pages/communities/threadDetail'],resolve),
     
    },
    {
      path:'/addresses',
      name:'addresses',
      component:resolve=> require(['@/pages/addresses'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/abort',
      name:'abort',
      component:resolve=> require(['@/pages/abort'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/operatorOrders',
      name:'operatorOrders',
      component:resolve=> require(['@/pages/myRepair/operatorOrders'],resolve),
      meta: {
        title:''
      }
    },
    {
      path:'/operatorOrdersDetail',
      name:'operatorOrdersDetail',
      component:resolve=> require(['@/pages/myRepair/operatorOrdersDetail'],resolve),
    
    },
    {
      path:'/operatorRepairSuccess',
      name:'operatorRepairSuccess',
      component:resolve=> require(['@/pages/myRepair/operatorRepairSuccess'],resolve),
    },


  ]
})

////
// to  from next() 

router.beforeEach((to, from, next) => {
  //title
  if(to.matched[0].name !== "index") {
    Vue.prototype.common.checkRegisterStatus()
  }

  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



